/* settings */
$burder-color: #fff
$header-scroll-bg: $txt-color
.header
  position: fixed
  top: 0
  left: 0
  width: 100%
  z-index: 10
  padding: 20px 0
  transition: all .3s
  &.scroll
    padding: 10px 0
    background-color: $header-scroll-bg
  & + main, & + section
    padding-top: 80px
    max-height: calc(100vh - 80px) !important
  &__content
    display: flex
    justify-content: space-between
    .logo-link
      color: $accent-color
      display: inline-block
      font-family: Montserrat, sans-serif
      font-weight: 700
      font-size: 16px
      line-height: 24px
      margin: 0 auto 0 0
      order: -2
      position: relative
      z-index: 10
      @media (min-width: 340px)
        font-size: 18px
      @media (min-width: 360px)
        font-size: 20px
      @media (min-width: 414px)
        font-size: 22px
      @media (min-width: 1200px)
        font-size: 24px
        line-height: 24px
        margin-right: 20px
      span
        color: #fff
    /* hamburger */
    .hamburger
      padding: 0
      display: flex
      cursor: pointer
      transition-property: opacity, filter
      transition-duration: 0.15s
      transition-timing-function: linear
      font: inherit
      color: inherit
      text-transform: none
      background-color: transparent
      border: 0
      overflow: visible
      position: relative
      order: 5
      z-index: 20
      margin: 0 0 0 20px
      @media (min-width: 1200px)
        display: none
      &:hover
        opacity: 0.7
      &.is-active
        &:hover
          opacity: 0.7
        .hamburger-inner
          background-color: $burder-color
          &::before, &::after
            background-color: $burder-color
    .hamburger-box
      width: 30px
      height: 24px
      display: inline-block
      position: relative
    .hamburger-inner
      display: block
      top: 50%
      margin-top: -2px
      width: 30px
      height: 3px
      background-color: $burder-color
      border-radius: 4px
      position: absolute
      transition-property: transform
      transition-duration: 0.15s
      transition-timing-function: ease
      &::before, &::after
        width: 30px
        height: 3px
        background-color: $burder-color
        border-radius: 4px
        position: absolute
        transition-property: transform
        transition-duration: 0.15s
        transition-timing-function: ease
      &::before, &::after
        content: ""
        display: block
      &::before
        top: -10px
      &::after
        bottom: -10px

    /* slide animate */
    .hamburger--slider
      .hamburger-inner
        top: 3px
        &::before
          top: 10px
          transition-property: transform, opacity
          transition-timing-function: ease
          transition-duration: 0.15s
        &::after
          top: 20px
      &.is-active .hamburger-inner
        transform: translate3d(0, 10px, 0) rotate(45deg)
        &::before
          transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0)
          opacity: 0
        &::after
          transform: translate3d(0, -20px, 0) rotate(-90deg)
    .account
      display: flex
      color: $accent-color
      margin: 0 0 0 20px
      position: relative
      z-index: 10
      .icon
        font-size: 25px
        line-height: 25px
      .cart-link
        position: relative
        display: inline-block
        margin-right: 20px
        padding-right: 10px
        .cart-count
          position: absolute
          top: -2px
          right: -12px
          font-size: 12px
          line-height: 12px
          font-weight: 700
          min-width: 20px
          text-align: left
      .account-menu
        position: relative
        .account-link
          cursor: pointer
        &__list
          position: absolute
          top: calc(100% + 12px)
          right: 0
          background-color: rgba(26,26,26,.97)
          padding: 10px
          border-radius: 5px
        &__item
          margin-bottom: 5px
          &:last-child
            margin-bottom: 0
        &__link
          font-size: 14px
          line-height: 20px
          color: #999
          transition: color .3s
          &:hover
            color: #fff

    .phone-link
      color: #fff
      font-weight: 700
      font-size: 20px
      line-height: 24px
      margin-right: 20px
      letter-spacing: .5px
      position: relative
      &:after
        content: ''
        display: block
        width: 0
        height: 1px
        background-color: transparent
        position: absolute
        bottom: -2px
        left: 0
        transition: background-color .3s, width .3s
      &:hover
        &:after
          width: 100%
          background-color: $accent-color
      @media (min-width: 768px)
        display: inline-block !important
        order: -1
        margin: 0
        white-space: nowrap
      @media (min-width: 1200px)
        order: 0
        margin-left: 40px
    .main-menu
      position: absolute
      top: 0
      left: 0
      width: 100%
      background-color: rgba($txt-color, .97)
      padding-top: 65px
      min-height: 0
      overflow: hidden
      transition: min-height .3s
      &.active
        min-height: 100vh
      @media (min-width: 1200px)
        display: block !important
        position: relative
        padding-top: 0
        min-height: auto
        flex: 1 1 auto
        background-color: transparent
      &__list
        display: flex
        flex-direction: column
        color: #fff
        padding: 15px 0
        text-transform: uppercase
        font-size: 18px
        line-height: 20px
        text-align: center

        @media (min-width: 1200px)
          flex-direction: row
          flex-wrap: wrap
          justify-content: flex-end
          padding: 0
      &__item
        display: block
        border-bottom: 1px solid #222
        padding: 10px 15px
        @media (min-width: 1200px)
          border-bottom: none
          padding: 0
          display: inline-block
          margin-left: 20px
          &:nth-of-type(1)
            margin-left: 0
        &:last-child
          border-bottom: none
        &.phone
          .phone-link
            margin: 0 auto
          @media (min-width: 768px)
            display: none !important
      &__link
        display: inline-block
        transition: color .3s
        @media (min-width: 1200px)
          color: #999
          font-weight: 600
          font-size: 14px
          line-height: 25px
        &:hover
          color: $accent-color
          @media (min-width: 1200px)
            color: #fff