/* all */
* {
  box-sizing: border-box;
  outline: none;
}
*::before, *::after {
  box-sizing: border-box;
}

body,
h1, h2, h3, h4, h5, h6, p,
ul[class], ol[class], li,
figure, figcaption, blockquote,
dl, dd {
  margin: 0;
}

ul[class], ol[class] {
  padding: 0;
  list-style-type: none;
}

button, label {
  cursor: pointer;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}
a[class] {
  text-decoration: none;
  color: inherit;
}

input, button, textarea, select {
  font: inherit;
}

img {
  max-width: 100%;
}

/* main */
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  position: relative;
  font-family: SourceSansPro, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #1a1a1a;
  background-color: #fff;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 8px;
}
body::-webkit-scrollbar-track {
  background: #373737;
}
body::-webkit-scrollbar-thumb {
  background: #fe323d;
}
body::-webkit-scrollbar-thumb:hover {
  background: #fe323d;
}
body.overflow {
  overflow: hidden;
}

.container {
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  max-width: 1170px;
}
@media (min-width: 415px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 768px) {
  .container {
    padding: 0 15px;
  }
}

/* settings */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 20px 0;
  transition: all 0.3s;
}
.header.scroll {
  padding: 10px 0;
  background-color: #1a1a1a;
}
.header + main, .header + section {
  padding-top: 80px;
  max-height: calc(100vh - 80px) !important;
}
.header__content {
  display: flex;
  justify-content: space-between;
  /* hamburger */
  /* slide animate */
}
.header__content .logo-link {
  color: #fe323d;
  display: inline-block;
  font-family: Montserrat, sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  margin: 0 auto 0 0;
  order: -2;
  position: relative;
  z-index: 10;
}
@media (min-width: 340px) {
  .header__content .logo-link {
    font-size: 18px;
  }
}
@media (min-width: 360px) {
  .header__content .logo-link {
    font-size: 20px;
  }
}
@media (min-width: 414px) {
  .header__content .logo-link {
    font-size: 22px;
  }
}
@media (min-width: 1200px) {
  .header__content .logo-link {
    font-size: 24px;
    line-height: 24px;
    margin-right: 20px;
  }
}
.header__content .logo-link span {
  color: #fff;
}
.header__content .hamburger {
  padding: 0;
  display: flex;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  overflow: visible;
  position: relative;
  order: 5;
  z-index: 20;
  margin: 0 0 0 20px;
}
@media (min-width: 1200px) {
  .header__content .hamburger {
    display: none;
  }
}
.header__content .hamburger:hover {
  opacity: 0.7;
}
.header__content .hamburger.is-active:hover {
  opacity: 0.7;
}
.header__content .hamburger.is-active .hamburger-inner {
  background-color: #fff;
}
.header__content .hamburger.is-active .hamburger-inner::before, .header__content .hamburger.is-active .hamburger-inner::after {
  background-color: #fff;
}
.header__content .hamburger-box {
  width: 30px;
  height: 24px;
  display: inline-block;
  position: relative;
}
.header__content .hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px;
  width: 30px;
  height: 3px;
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.header__content .hamburger-inner::before, .header__content .hamburger-inner::after {
  width: 30px;
  height: 3px;
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.header__content .hamburger-inner::before, .header__content .hamburger-inner::after {
  content: "";
  display: block;
}
.header__content .hamburger-inner::before {
  top: -10px;
}
.header__content .hamburger-inner::after {
  bottom: -10px;
}
.header__content .hamburger--slider .hamburger-inner {
  top: 3px;
}
.header__content .hamburger--slider .hamburger-inner::before {
  top: 10px;
  transition-property: transform, opacity;
  transition-timing-function: ease;
  transition-duration: 0.15s;
}
.header__content .hamburger--slider .hamburger-inner::after {
  top: 20px;
}
.header__content .hamburger--slider.is-active .hamburger-inner {
  transform: translate3d(0, 10px, 0) rotate(45deg);
}
.header__content .hamburger--slider.is-active .hamburger-inner::before {
  transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
  opacity: 0;
}
.header__content .hamburger--slider.is-active .hamburger-inner::after {
  transform: translate3d(0, -20px, 0) rotate(-90deg);
}
.header__content .account {
  display: flex;
  color: #fe323d;
  margin: 0 0 0 20px;
  position: relative;
  z-index: 10;
}
.header__content .account .icon {
  font-size: 25px;
  line-height: 25px;
}
.header__content .account .cart-link {
  position: relative;
  display: inline-block;
  margin-right: 20px;
  padding-right: 10px;
}
.header__content .account .cart-link .cart-count {
  position: absolute;
  top: -2px;
  right: -12px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 700;
  min-width: 20px;
  text-align: left;
}
.header__content .account .account-menu {
  position: relative;
}
.header__content .account .account-menu .account-link {
  cursor: pointer;
}
.header__content .account .account-menu__list {
  position: absolute;
  top: calc(100% + 12px);
  right: 0;
  background-color: rgba(26, 26, 26, 0.97);
  padding: 10px;
  border-radius: 5px;
}
.header__content .account .account-menu__item {
  margin-bottom: 5px;
}
.header__content .account .account-menu__item:last-child {
  margin-bottom: 0;
}
.header__content .account .account-menu__link {
  font-size: 14px;
  line-height: 20px;
  color: #999;
  transition: color 0.3s;
}
.header__content .account .account-menu__link:hover {
  color: #fff;
}
.header__content .phone-link {
  color: #fff;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  margin-right: 20px;
  letter-spacing: 0.5px;
  position: relative;
}
.header__content .phone-link:after {
  content: "";
  display: block;
  width: 0;
  height: 1px;
  background-color: transparent;
  position: absolute;
  bottom: -2px;
  left: 0;
  transition: background-color 0.3s, width 0.3s;
}
.header__content .phone-link:hover:after {
  width: 100%;
  background-color: #fe323d;
}
@media (min-width: 768px) {
  .header__content .phone-link {
    display: inline-block !important;
    order: -1;
    margin: 0;
    white-space: nowrap;
  }
}
@media (min-width: 1200px) {
  .header__content .phone-link {
    order: 0;
    margin-left: 40px;
  }
}
.header__content .main-menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(26, 26, 26, 0.97);
  padding-top: 65px;
  min-height: 0;
  overflow: hidden;
  transition: min-height 0.3s;
}
.header__content .main-menu.active {
  min-height: 100vh;
}
@media (min-width: 1200px) {
  .header__content .main-menu {
    display: block !important;
    position: relative;
    padding-top: 0;
    min-height: auto;
    flex: 1 1 auto;
    background-color: transparent;
  }
}
.header__content .main-menu__list {
  display: flex;
  flex-direction: column;
  color: #fff;
  padding: 15px 0;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 20px;
  text-align: center;
}
@media (min-width: 1200px) {
  .header__content .main-menu__list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    padding: 0;
  }
}
.header__content .main-menu__item {
  display: block;
  border-bottom: 1px solid #222;
  padding: 10px 15px;
}
@media (min-width: 1200px) {
  .header__content .main-menu__item {
    border-bottom: none;
    padding: 0;
    display: inline-block;
    margin-left: 20px;
  }
  .header__content .main-menu__item:nth-of-type(1) {
    margin-left: 0;
  }
}
.header__content .main-menu__item:last-child {
  border-bottom: none;
}
.header__content .main-menu__item.phone .phone-link {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__content .main-menu__item.phone {
    display: none !important;
  }
}
.header__content .main-menu__link {
  display: inline-block;
  transition: color 0.3s;
}
@media (min-width: 1200px) {
  .header__content .main-menu__link {
    color: #999;
    font-weight: 600;
    font-size: 14px;
    line-height: 25px;
  }
}
.header__content .main-menu__link:hover {
  color: #fe323d;
}
@media (min-width: 1200px) {
  .header__content .main-menu__link:hover {
    color: #fff;
  }
}

/* main page */
.main-block {
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  min-height: 100vh;
  background-color: #4e4846;
  position: relative;
}
.main-block:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 0;
  left: 0;
}
.main-block__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  flex-wrap: wrap;
  min-height: calc(100vh - 70px);
  color: #fff;
  position: relative;
  z-index: 1;
}
.main-block__subtitle {
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  display: inline-block;
  margin-bottom: 10px;
}
@media (min-width: 415px) {
  .main-block__subtitle {
    font-size: 18px;
  }
}
@media (min-width: 768px) {
  .main-block__subtitle {
    font-size: 20px;
    margin-bottom: 15px;
  }
}
@media (min-width: 960px) {
  .main-block__subtitle {
    font-size: 22px;
    line-height: 22px;
  }
}
@media (min-width: 1200px) {
  .main-block__subtitle {
    font-size: 24px;
    line-height: 24px;
  }
}
.main-block__title {
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  font-size: 19px;
  line-height: 24px;
  display: inline-block;
  margin-bottom: 10px;
}
@media (min-width: 340px) {
  .main-block__title {
    font-size: 20px;
  }
}
@media (min-width: 360px) {
  .main-block__title {
    font-size: 21px;
  }
}
@media (min-width: 414px) {
  .main-block__title {
    font-size: 22px;
  }
