  position: relative;
  overflow: hidden;
  transition: border-color 0.3s;
  margin-bottom: 15px;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0.4);
}
@media (min-width: 415px) {
  .main-block .enterprises-link {
