/* catalog page */
.catalog-header {
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  background-color: #4e4846;
}
.catalog-header__title {
  color: #fff;
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 22px;
  line-height: 24px;
  letter-spacing: 0.55px;
}
.catalog-header .search-form {
  margin: 20px 0;
}
.catalog-header .search-form .input-group {
  background-color: rgba(255, 255, 255, 0.9);
  display: flex;
}
.catalog-header .search-form .input-group button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  background-color: transparent;
  font-size: 28px;
  line-height: 20px;
  padding: 0 10px;
}
.catalog-header .search-form .input-group input {
  flex: 1 1 100%;
  border: none;
  border-radius: 0;
  background-color: transparent;
  padding: 10px;
  font-size: 18px;
  line-height: 20px;
  font-weight: 100;
}
.catalog-header .search-form .input-group input.placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
}
.catalog-header .search-form .input-group input::-webkit-input-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
}
.catalog-header .search-form .input-group input::-moz-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
}
.catalog-header .search-form .input-group input:-moz-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
}
.catalog-header .search-form .input-group input:-ms-input-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
}
.catalog-header .search-form .input-group input:focus.placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.catalog-header .search-form .input-group input:focus::-webkit-input-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.catalog-header .search-form .input-group input:focus::-moz-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.catalog-header .search-form .input-group input:focus:-moz-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.catalog-header .search-form .input-group input:focus:-ms-input-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.catalog-menu {
  background-color: #1a1a1a;
  padding: 15px 0;
  color: #999;
}
.catalog-menu .catalog-link {
  display: flex;
  flex: 1 1 100%;
  text-transform: uppercase;
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  line-height: 20px;
  letter-spacing: 0.5px;
  color: #fff;
}
.catalog-menu .catalog-link .icon {
  margin-right: 15px;
}
.catalog-menu .catalog-link .icon.icon-down-open-mini {
  margin: 0 0 0 auto;
  font-size: 24px;
  line-height: 20px;
}
.catalog-menu__item {
  padding: 8px 10px;
  border-bottom: 1px solid #222;
}
.catalog-menu__item:nth-of-type(1) {
  margin-top: 15px;
}
.catalog-menu__item:last-child {
  border-bottom: none;
}
.catalog-menu__link {
  display: block;
  font-family: Montserrat, sans-serif;
  transition: color 0.3s;
  font-size: 16px;
  line-height: 20px;
}
.catalog-menu__link:hover {
  color: #fff;
}

.breadcrumbs {
  padding: 15px 0;
}
.breadcrumbs .breadcrumbs-list {
  display: flex;
  flex-wrap: wrap;
  flex: 1 1 100%;
  font-size: 15px;
  line-height: 20px;
}
.breadcrumbs .breadcrumbs-list__item {
  position: relative;
  margin-bottom: 5px;
  margin-right: 10px;
}
.breadcrumbs .breadcrumbs-list__item:before {
  content: "/";
  display: inline-block;
  margin: 0 10px 0 0;
}
.breadcrumbs .breadcrumbs-list__item:nth-of-type(1):before {
  display: none;
}
.breadcrumbs .breadcrumbs-list__link {
  transition: color 0.3s;
}
.breadcrumbs .breadcrumbs-list__link:hover {
