  font-size: 18px;
  line-height: 20px;
}
.catalog__content__left-side .filter-link .icon {
  margin-right: 10px;
  font-size: 24px;
  line-height: 20px;
}
.catalog__content__left-side .filter-link .icon.icon-down-open-mini {
  margin: 0 0 0 auto;
  font-size: 24px;
  line-height: 20px;
  transform: scale(1, 1);
