@import "settings"                                                                                                      // settings
@import "_mixins/mixins_list"                                                                                           // mixins
@import "_fonts/fonts_list"                                                                                             // fonts
@import "_fonts/fontello"                                                                                            // icons font
@import "_parts/reset"                                                                                                  // reset styles
@import "_parts/header"                                                                                                 // header

/* main page */
.main-block
  background-repeat: no-repeat
  background-position: top center
  background-size: cover
  min-height: 100vh
  background-color: #4e4846
  position: relative
  &:before
    content: ''
    display: block
    width: 100%
    height: 100%
    background-color: rgba(0,0,0,.3)
    position: absolute
    top: 0
    left: 0
  &__content
    display: flex
    flex-direction: column
    align-items: flex-start
    justify-content: flex-end
    flex-wrap: wrap
    min-height: calc(100vh - 70px)
    color: #fff
    position: relative
    z-index: 1
  &__subtitle
    font-family: Montserrat, sans-serif
    font-weight: 400
    font-size: 16px
    line-height: 20px
    display: inline-block
    margin-bottom: 10px
    @media (min-width: 415px)
      font-size: 18px
    @media (min-width: 768px)
      font-size: 20px
      margin-bottom: 15px
    @media (min-width: 960px)
      font-size: 22px
      line-height: 22px
    @media (min-width: 1200px)
      font-size: 24px
      line-height: 24px
  &__title
    font-family: Montserrat, sans-serif
    font-weight: 400
    font-size: 19px
    line-height: 24px
    display: inline-block
    margin-bottom: 10px
    @media (min-width: 340px)
      font-size: 20px
    @media (min-width: 360px)
      font-size: 21px
    @media (min-width: 414px)
      font-size: 22px
    @media (min-width: 415px)
      font-size: 24px
    @media (min-width: 768px)
      font-size: 32px
      line-height: 32px
    @media (min-width: 960px)
      font-size: 42px
      line-height: 42px
    @media (min-width: 1200px)
      font-size: 45px
      line-height: 45px
  &__descr
    font-size: 14px
    line-height: 20px
    margin-bottom: 30px
    @media (min-width: 340px)
      font-size: 15px
    @media (min-width: 360px)
      font-size: 16px
    @media (min-width: 415px)
      font-size: 18px
      font-weight: 100
      margin-top: 15px
    @media (min-width: 960px)
      font-size: 19px
      line-height: 20px
    @media (min-width: 1200px)
      margin-bottom: 50px
    p
      margin-bottom: 10px
  .enterprises-link
    display: block
    width: 100%
    border: 1px solid #aaa
    padding: 15px 0
    text-align: center
    color: #fff
    position: relative
    overflow: hidden
    transition: border-color .3s
    margin-bottom: 15px
    text-transform: uppercase
    background-color: rgba(0,0,0,.4)
    @media (min-width: 415px)
      margin-bottom: 30px
    @media (min-width: 768px)
      max-width: 300px
      margin: 0 auto 30px
    @media (min-width: 1200px)
      margin: 0 auto 50px
    span
      position: relative
      z-index: 2
    &:before
      content: ''
      display: block
      width: 100%
      height: 100%
      position: absolute
      background-color: $accent-color
      top: 0
      left: 0
      transform: translateY(100%)
      transition: transform .3s
    &:hover
      border-color: $accent-color
      &:before
        transform: translateY(0)

/* catalog page */
.catalog-header
  background-repeat: no-repeat
  background-position: top center
  background-size: cover
  background-color: #4e4846
  &__title
    color: #fff
    font-family: Montserrat, sans-serif
    font-weight: 400
    text-transform: uppercase
    font-size: 22px
    line-height: 24px
    letter-spacing: .55px
  .search-form
    margin: 20px 0
    .input-group
      background-color: rgba(255,255,255,.9)
      display: flex
      button
        display: flex
        align-items: center
        justify-content: center
        border: none
        background-color: transparent
        font-size: 28px
        line-height: 20px
        padding: 0 10px
      input
        flex: 1 1 100%
        border: none
        border-radius: 0
        background-color: transparent
        padding: 10px
        font-size: 18px
        line-height: 20px
        font-weight: 100
        @include input-placeholder
          opacity: 1
          transition: opacity 0.3s ease
        &:focus
          @include input-placeholder
            opacity: 0
            transition: opacity 0.3s ease
.catalog-menu
  background-color: $txt-color
  padding: 15px 0
  color: #999
  .catalog-link
    display: flex
    flex: 1 1 100%
    text-transform: uppercase
    font-family: Montserrat, sans-serif
    font-size: 18px
    line-height: 20px
    letter-spacing: .5px
    color: #fff
    .icon
      margin-right: 15px
      &.icon-down-open-mini
        margin: 0 0 0 auto
        font-size: 24px
        line-height: 20px
  &__item
    padding: 8px 10px
    border-bottom: 1px solid #222
    &:nth-of-type(1)
      margin-top: 15px
    &:last-child
      border-bottom: none
  &__link
    display: block
    font-family: Montserrat, sans-serif
    transition: color .3s
    font-size: 16px
    line-height: 20px
    &:hover
      color: #fff

.breadcrumbs
  padding: 15px 0
  .breadcrumbs-list
    display: flex
    flex-wrap: wrap
    flex: 1 1 100%
    font-size: 15px
    line-height: 20px
    &__item
      position: relative
      margin-bottom: 5px
      margin-right: 10px
      &:before
        content: '/'
        display: inline-block
        margin: 0 10px 0 0
      &:nth-of-type(1)
        &:before
          display: none
    &__link
      transition: color .3s
      &:hover
        color: $accent-color

.catalog
  &__content
    &__left-side
      margin: 15px 0
      border: 1px solid #aaa
      .filter-link
        display: flex
        flex: 1 1 100%
        padding: 10px
        font-size: 18px
        line-height: 20px
        .icon
          margin-right: 10px
          font-size: 24px
          line-height: 20px
          &.icon-down-open-mini
            margin: 0 0 0 auto
            font-size: 24px
            line-height: 20px
            transform: scale(1,1)
            transition: transform .3s
        &.open
          .icon-down-open-mini
            transform: scale(1,-1)
      .product-filters-block
        border-top: 1px solid #aaa
        padding: 10px 12px
    &__right-side
      margin: 0














